.barradenav{
    display: flex;
    justify-content: center;
    text-align: center;
    background-color: white;
    padding: 20px;
}
  
.titulo{
    display: flex;
    justify-content: left;
    text-align: left;
    background-color: rgba(195, 248, 255, 0);
    border: 1px solid rgba(128, 128, 128, 0.603);
    box-shadow: 0 1px 20px rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(8.5px);
    padding: 19px;
}
